import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-events-widget',
  templateUrl: './events-widget.component.html',
  styleUrls: ['./events-widget.component.scss']
})
export class EventsWidgetComponent implements OnInit, OnDestroy {
  forexdata;
  interval;
  constructor(private http: HttpClient) {
   }
  //  https://calendarsignalapi.herokuapp.com/trading/forex
  forexData() {
    // this.http.get('http://5.189.170.190:3000/trading/currencies').subscribe((res: any) => {
    //   this.forexdata = res.data[0];
    //   });
      this.http.get('https://calendarsignalapi.herokuapp.com/trading/forex').subscribe((res: any) => {
      this.forexdata = res.data;
      console.log(res.data);
      });
  }

  ngOnInit() {
    this.forexData();
    this.interval = setInterval(() => {
      this.forexData();
    }, 500);
  }

  ngOnDestroy() {
      clearInterval(this.interval);
  }

}
