import { HeaderService } from './../../../../../shared/services/header.service';
import { GlobalService } from './../../../../../shared/services/global.service';
import { LayoutService } from './../../../../../shared/services/layout.service';
import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Chart } from 'chart.js';
@Component({
  selector: 'app-status-widget',
  templateUrl: './status-widget.component.html',
  styleUrls: ['./status-widget.component.scss']
})
export class StatusWidgetComponent implements OnInit, AfterViewInit {
  public colorTheme: string;
  public data: any;
  public IbPartners: any[] = [];
  public options: any;
  public walletDetails;
  public walletBalance;
  public LoggedParams;
  public kycStatus;
  public profileAccounts;
  public minWithdrawMinDeposit;
  public piedata;
  public ledgerDetail;
  constructor(
    private layoutService: LayoutService,
    private global: GlobalService,
    private headerService: HeaderService
    ) {
      this.headerService.castLoggedUserDetail.subscribe(
        resData => (this.LoggedParams = resData)
      );
      this.kycStatus =  this.LoggedParams.sProfile.uKYCStatus;
    }

  ngOnInit() {
    this.layoutService.castContentSectionTheme.subscribe(
      theme => (this.colorTheme = theme)
    );
    this.getWallet();
    this.getIbPartners();
    this.walletBalanse();
    this.getProfileAccounts();
  }
  getIbPartners() {
    const data = {
      MasterId: this.LoggedParams.sProfile.sMasterId
    };
    this.global.getIbPartner(data).subscribe((res: any) => {
      this.IbPartners = res;
    }, err => {
      console.log(err);
    });

  }
  walletBalanse() {
    this.headerService.castWalletBalance.subscribe(
      resData => (this.walletBalance = resData)
    );
  }
  getWallet() {
    const data: any = {
      sWalletId: this.LoggedParams.sProfile.sWalletId
    };
    this.global.getWalletInfo(data).subscribe((wallet: any) => {
      this.walletDetails = wallet;
    });
  }
  getProfileAccounts() {
    const data = {
      ProfileId: localStorage.getItem('LoggedDealermasterid'),
      LoginId:  localStorage.getItem('LoggedDealermasterid')
    };
    this.global.getProfileAccounts(data).subscribe((res: any) => {
      this.profileAccounts = res.lstAccount;
      this.minWithdrawMinDeposit = res.lstAccount[0];
      console.log(this.minWithdrawMinDeposit);
    }, err => {
      console.log(err);
    });
  }

  ngAfterViewInit() {
  }

}
