import { LayoutService } from './../../../../../shared/services/layout.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-links-widget',
  templateUrl: './links-widget.component.html',
  styleUrls: ['./links-widget.component.scss']
})
export class LinksWidgetComponent implements OnInit {
  colorTheme: string;
  pageVisitsData: any;
  constructor(private layoutService: LayoutService) { }

  ngOnInit(): void {
    this.layoutService.castContentSectionTheme.subscribe(
      theme => (this.colorTheme = theme)
    );
  }

}
