import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'removePercentage'
})
export class RemovePercentagePipe implements PipeTransform {

  transform(value: any): any {
    const str = value.replace(/%/g, '');
    if (str > 0) {
      return true;
    } else {
      return false;
    }
  }
}

