import { HandleScreenBarService } from './../../../../shared/services/handle-screen-bar.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { GlobalService } from '../../../../shared/services/global.service';
import { HeaderService } from '../../../../shared/services/header.service';

@Component({
  selector: 'app-client-dashboard-core',
  templateUrl: './client-dashboard-core.component.html',
  styleUrls: ['./client-dashboard-core.component.scss']
})
export class ClientDashboardCoreComponent implements OnInit, OnDestroy {
  public data: any;
  public IbPartners: any[] = [];
  public options: any;
  public walletDetails: any;
  public walletBalance: any;
  public LoggedParams: any;
  public kycStatus: any;
  public profileAccounts: any;
  public minWithdrawMinDeposit: any;
  public piedata: any;
  public ledgerDetail: any;
  constructor(
    private global: GlobalService,
    private headerService: HeaderService,
    private handleScreenBarService: HandleScreenBarService,
    ) {
  this.handleScreenBarService.getScreenTitlevisibility(false);
  this.headerService.castLoggedUserDetail.subscribe(
      resData => (this.LoggedParams = resData)
    );
  this.kycStatus =  this.LoggedParams.sProfile.uKYCStatus;
   }

   getIbPartners() {
    const data = {
      MasterId: localStorage.getItem('LoggedDealermasterid')
    };
    this.global.getIbPartner(data).subscribe((res: any) => {
      this.IbPartners = res;
    }, err => {
      console.log(err);
    });
  }

  walletBalanse() {
    this.headerService.castWalletBalance.subscribe(
      resData => (this.walletBalance = resData)
    );
  }

  getWallet() {
    const data: any = {
      sWalletId: this.LoggedParams.sProfile.sWalletId
    };
    this.global.getWalletInfo(data).subscribe((wallet: any) => {
      this.walletDetails = wallet;
    });
  }

  getProfileAccounts() {
    const data = {
      ProfileId: localStorage.getItem('LoggedDealermasterid'),
      LoginId:  localStorage.getItem('LoggedDealermasterid')
    };
    this.global.getProfileAccounts(data).subscribe((res: any) => {
      this.profileAccounts = res.lstAccount;
      this.minWithdrawMinDeposit = res.lstAccount[0];
      console.log(this.minWithdrawMinDeposit);
    }, err => {
      console.log(err);
    });
  }



  ngOnInit(): void {
    this.getWallet();
    this.getIbPartners();
    this.walletBalanse();
    this.getProfileAccounts();
  }
  ngOnDestroy() {
    this.handleScreenBarService.getScreenTitlevisibility(true);
  }

}
