import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-signal-widget',
  templateUrl: './signal-widget.component.html',
  styleUrls: ['./signal-widget.component.scss']
})
export class SignalWidgetComponent implements OnInit, OnDestroy {
  forexdata;
  interval;
  constructor(private http: HttpClient) { }
  forexData() {
    // this.http.get('http://localhost:3000/trading/currencies').subscribe((res: any) => {
    //   this.forexdata = [].concat(...res.data);
    //   console.log(this.forexdata);
    //   });
    this.http.get(' https://calendarsignalapi.herokuapp.com/trading/allForexSignal').subscribe((res: any) => {
      this.forexdata = [].concat(...res.data);
      console.log(this.forexdata);
      });
  }
  ngOnInit() {
    this.forexData();
    this.interval = setInterval(() => {
      this.forexData();
    }, 500);
  }
  ngOnDestroy() {
      clearInterval(this.interval);
  }
}
