import { Component, OnInit } from '@angular/core';
import { GlobalService } from '../../../../../shared/services/global.service';
import { HeaderService } from '../../../../../shared/services/header.service';

@Component({
  selector: 'app-transaction-history-widget',
  templateUrl: './transaction-history-widget.component.html',
  styleUrls: ['./transaction-history-widget.component.scss']
})
export class TransactionHistoryWidgetComponent implements OnInit {
  LoggedParams;
  ledgerDetail: any[] = [];
  constructor(private global: GlobalService, private headerService: HeaderService) {

    this.headerService.castLoggedUserDetail.subscribe(
       resData => (this.LoggedParams = resData)
     );
   }
  getLedgerDetails() {
    const data: any = {
      WalletId: this.LoggedParams.sProfile.sWalletId,
      DealerID: this.LoggedParams.sProfile.sDealerId,
      Initial: 1,
      MaxCount: 10
    };
    this.global.getLedger(data).subscribe((ledger: any) => {
    this.ledgerDetail = ledger.sLedger.lstLedger;
    console.log(this.ledgerDetail);
  }, err => {
    console.log(err);
  });
}
  ngOnInit(): void {
    this.getLedgerDetails();
  }

}
